:root { 
    --dark-blue: #081143;
    --blue: #086788;
    --pink: #E0ACD5;
    --red: #BA1200;
    --light-blue: #8ce5e1;
}


body {
    background-color: white;
    font-family: 'Inter', sans-serif;
}

body,html {
    scroll-behavior: smooth;
}

section {
    min-height: 100vh;
}

@media (prefers-reduced-motion){
    body,html {
        scroll-behavior: initial;
    }
}