.navbar {
    position: fixed;
    top:0;
    width: 100vw;
    z-index: 9;
    display: flex;
    justify-content: space-between;
    padding: 10px;
    color: white;
    font-weight: 200;
    letter-spacing: 2px;
    background-image: linear-gradient(rgba(0,0,0,0.4), rgba(0,0,0,0));
    height: 40px;
    transition: all 0.5s ease;
    -webkit-transition: all 0.5s ease;
}

.navbar li {
    list-style: none;
    cursor: pointer;
    transition: all 0.5s ease;
}

.navbar ul {
    display: flex;
}

.navbar ul li {
    margin: 0 15px;
}

.nav-link {
    all: unset;
    transition: all 0.5 ease;
    -webkit-transition: all 0.3s ease;
}

.nav-link:hover{
    font-weight: 600;
}

.active {
    font-weight: 600;
    border-bottom: 2px solid white;
    border-radius: 2px;
}

.darker {
    background-image: linear-gradient(rgba(0,0,0,0.7), rgba(0,0,0,0.1));
}
