.about-container {
    min-height: 100vh;
    /* overflow: hidden; */
    position: relative;

}

.about-content{
    margin: 0 auto;
    height: 72vh;
    width: 120vh;
    min-height: 720px;
    max-width: 1200px;
    background-color: black;
    display: flex;
    flex-direction:row-reverse;
    background-color: white;
    box-shadow: 8px 15px 30px rgba(0,0,0,0.4);
    border-radius: 30px;
    overflow: hidden;
}

.about-left{
    position: relative;
    overflow: hidden;
    background-color: white;
    width: 48%;
}

.about-right {
    background-image: radial-gradient(#c75346, #9f5148);
    width: 60%;
    position: relative;
    overflow: hidden;
    
}

.about-section{
    position: absolute;
    height: 200px;
    width: 450px;
    background-color: white;
    box-shadow: 0 10px 15px rgba(0,0,0,0.4);
    padding: 15px 25px 15px 25px;
    color: #820d03;
    font-weight: 500;
    font-size: 1.5rem;
    font-family: 'Caveat', cursive;
    line-height: 42px;
}

.about-first {
    left: -8px;
    top: 10%;
    transform: rotate(-5deg);
}

.about-second {
    right: -8px;
    top: 45%;
    transform: rotate(5deg);
}

.about-third {
    width: 186px;
    height: 62px;
    transform: rotate(-3deg);
    left: -8px;
    top: 80%;
}

.about-container strong {
    font-weight: 700;
}

.icon-link a {
    font-size: 2rem;
    
    transition: all 0.3 ease;
    -webkit-transition: all 0.3 ease;
}

.linkedin a{
    color: rgb(28, 96, 223);
}

.github a {
    color: rgb(138, 17, 138);
    margin: 0 20px;
}

.instagram a {
    color: black;
}

.about-title{
    margin: 0 auto;
    text-align: center;
    font-size: 2.5rem;
    padding: 50px 0;
    font-weight: 300;
    color: var(--red);
    text-shadow: 3px 3px 0 rgba(0,0,0,0.1);
}

.code-container {
    height: 600px;
    background-color: rgb(63, 63, 63);
    overflow: auto;
    box-shadow: inset 0 0 20px black;
    color: white;
    padding: 30px;
    font-size: 1rem;
    font-family: monospace;
}


.code-container a {
    all: unset;
    text-decoration: underline;
    cursor: pointer;
}



.cB {color: #3487b9}
.cG {color: #00ccaf}
.cGd {color: #ffd500}
