.i2{margin-left: 80px}
.i3{margin-left: 120px}
.i4{margin-left: 160px}

@media only screen and (max-width: 1200px) { 
    .about-content {
        background-image: radial-gradient(#c75346, #9f5148);
        width: 100%;
        border-radius: 0;
    }
    .about-left {
        width: 100%;
    }
    .about-right {
        width: 100%;
        background-image: none;
    }
    .about-first, .about-third {
        right: 0;
        left: auto;
    }

}

@media only screen and (max-width: 934px) { 
    .about-left {
        display: none;
    }
    .about-first, .about-third {
        left: -8px;
    }

}

@media only screen and (max-width: 450px) { 

    .about-section {
        width: 270px;
        line-height: 20px;
        font-size: 1.3rem;
        height: auto;
        
    }
    .about-third {
        width: 190px;
    }
    .about-title {
        padding: 10px 0;
    }
}

