article {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    opacity: 0;
    transition: all 0.5s ease;
  }
  .activeSlide {
    opacity: 1;
    transform: translateX(0);
  }
  .lastSlide {
    opacity: 0;
    transform: translateX(-100%);
  }
  .nextSlide {
    transform: translateX(100%);
    opacity: 0;
  }

  .carousel-container {
    height: 100%;
  }

  .carousel {
    width: 600px;
    display: flex;
    height: 100%;
    transition: all 0.3s linear;
  }

  .carousel article {
    transition: all 0.5s ease;
  }

  .carousel img {
    position: absolute;
    top: 0;
    left: 50%;
    transform: translateX(-50%);
    
    height: 100%;
    object-fit: cover;
    aspect-ratio: 4/5;
  }

  .carousel button {
    position: absolute;
    transform: translateY(-50%);
    z-index: 2;
    background-color: rgba(0,0,0,0.3);
    color: inherit;
    border: none;
    padding: 0;
    cursor: pointer;
    font-size: 2rem;
    color: white;
    padding: 5px 0 0;
    transition: all 0.3s ease;
    -webkit-transition: all 0.3s ease;
  
  }

  .carousel button:hover {
    background-color: rgba(0,0,0,0.5);
  }

 .prev {
    top: 50%;
    left: 0;
    border-radius: 0 12px 12px 0;
  }

  .next {
    top: 50%;
    right: 0;
    border-radius: 12px 0 0 12px;
  }

