.home-container {
    height: 100vh;
    background-image: radial-gradient(#85e0db, #3f8481);
    position: relative;
    padding: 30px;
}

.down {
    color: black;
    font-size: 3rem;
}

.down-container{
    position: absolute;
    bottom: 0;
    left: 50%;
    transform: translateX(-50%);
    cursor: pointer;
    z-index: 99;
    animation: bounce 2s ease 5s infinite;
}

@keyframes bounce {
    0%, 10%, 25%, 40%, 50% {transform: translate(-50%, 0);} 
            20% {transform: translate(-50%, -30px);} 
            30% {transform: translate(-50%, -15px);} 
}


.home-content {
    display: flex;
    flex-wrap:wrap;
    justify-content: space-evenly;
    align-items: center;
    position: relative;
    top: 50%;
    transform: translateY(-50%);
    width: 90%;
    margin: 0 auto;
}

#profile-pic {
    height: 400px;
}

.home-text h1 {
    font-size: 3.5rem;
    
    color: var(--dark-blue);
    text-shadow: 6px 6px 1px rgba(0,0,0,0.05);

    
}

.home-text{
     padding: 20px;
     width: 50%;
}

#hello {
    font-weight: 700;
    font-size: 2rem;
    color: var(--red);
    text-shadow: 3px 3px 1px rgba(0,0,0,0.05);
}

.home-container strong {
    color: var(--red);
    text-shadow: 3px 3px 1px rgba(0,0,0,0.05);
}

.home-text p {
    font-size: 1.2rem;
    color: var(--dark-blue);
    max-width: 530px;
}


.home-text > *{
    margin: 10px 0;
}


@media only screen and (max-width: 1270px) {
    #hello {
        font-size: 1.2rem;
    }
    .home-text h1 {
        font-size: 2rem;
    }

    .home-content{
        width: 100%;
    }

    .home-text p {
        font-size: 0.8rem;
    }
    #profile-pic {
        height: 300px;
    }



   
}
@media only screen and (max-width: 800px) {
    .home-content {
        display: block;
        text-align: center;
    }
    .home-text {
        margin: 0 auto;
        width: 80%;
        text-align: center;
    }
    .navbar {
        font-size: 0.8rem;
        justify-content: center;
    }
    #profile-pic {
        height: 200px;
    }
    .code-container {
        font-size: 0.8rem;
        white-space: nowrap;
        padding: 20px;
        height: 450px;
    }
    .i1{margin-left: 20px}
    .i2{margin-left: 40px}
    .i3{margin-left: 60px}
    .i4{margin-left: 80px}

}
@media only screen and (max-width: 523px) {

    .navbar {
        font-size: 0.5rem;
        padding: 10px 10px;
    }
    .navbar li {
        margin: 0 15px;
    }
    #hello {
        font-size: 0.9rem;
    }
    .home-text h1 {
        font-size: 1.5rem;
    }

}

@media only screen and (max-width: 400px) {
    .navbar ul li {
        margin: 0 5px;
    }
}